}
.header-new__socials-wrap .icon-instagram:hover {
  color: #C13584;
}
.header-new__socials-wrap .icon-linkedin:hover {
  color: #2867B2;
}
.header-new__socials-wrap .icon-twitter:hover {
  color: #1DA1F2;
}
.header-new__socials-wrap .icon-youtube:hover {
  color: #FF0000;
}
.header-new__socials-wrap .icon-vk:hover {
  color: #2787F5;
}
.header-new__button {
  display: none;
}
.header-new__feedback {
  display: none;
}
.menu-item-has-children.current-menu-parent > a::before,
.menu-item-has-children.current-menu-item > a::before {
  content: '';
  position: absolute;
  right: 10px;
  top: 50%;
  background-image: url("../../img/header-angle-right-white.svg") !important;
